.app-bar__brand {\n  display: flex;\n  align-items: center;\n}\n \n.app-bar .app-bar__brand h1 {\n  color: #db0000;\n  text-transform: uppercase;\n  font-size: 16px;\n  user-select: none;\n}\n \n.app-bar .app-bar__navigation {\n  display: flex;\n  justify-content: start;\n  position: absolute;\n  top: 90px;\n  left: -100%;\n  width: 100%;\n  transition: all 0.3s;\n  padding: 8px 0px;\n  background-color: white;\n  overflow: hidden;\n  z-index: 0;\n}\n \n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n \n.app-bar .app-bar__navigation > ul > li > a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  padding: 14px 0px;\n  margin-bottom: 5px;\n  width: 100%;\n}\n  \n  #gambarhero {\n    width: 100%;\n    height: 500px;\n    background-color: $red;\n    object-fit: none;\n    left: 0;\n    right: 0;\n    margin: 0;\n  }\n  #hero {\n    width: 100%;\n  }\n  #hero > #hero-text {\n    margin-top: -300px;\n    padding-bottom: 300px;\n    text-align: center;\n    color: white;\n    font-size: 24px;\n    color: white;\n    text-shadow: 2px 2px 4px #000000;\n    padding-left:  10%;\n    padding-right: 10%;\n    button {\n    background-color:white;\n    min-height: 44px;\n    min-width: 44px;\n    padding: 0 10px;\n    border: none;\n    border-radius: 5px;\n    cursor: pointer;\n    color: black;\n    a {\n      color: white;\n      text-decoration: none;\n    }\n    }\n  }\n  /* Styling button \"Login\" */\n.button-container {\n  display: flex;\n  align-items: center;\n}\n\n.button-container .button {\n  margin-right: 10px;\n  padding: 8px 16px;\n  border: none;\n  background-color: #db0000; /* Warna latar belakang */\n  color: white; /* Warna teks */\n  text-decoration: none;\n  border-radius: 4px;\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n}\n\n.button-container .button:hover {\n  background-color: #b80000; /* Warna latar belakang saat hover */\n}\n\n  // content\n\n  .poster {\n    img {\n      width: 180px;\n    }\n  }\n\n  #new-event {\n    margin-top: -200px;\n    display: flex;\n    flex-direction: column;\n   align-items: center;\n   gap: 20px;\n   button:hover {\n    background-color: $red;\n  }\n  button {\n  background-color:$red;\n  min-height: 44px;\n  min-width: 44px;\n  padding: 0 10px;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  color: white;\n  a {\n    color: white;\n    text-decoration: none;\n  }\n  }\n}\n  \n  .event-container {\n    display: flex;\n    flex-direction: row ;\n    gap: 20px;\n  }\n  #show-event, #profile-event {\n    display: grid;\n    grid-template-columns: repeat(2 , 1fr); gap: 20px;\n    font-size: 16px;\n  }\n  \n\n  .event-wrapper {\n    display: flex;\n    flex-direction: row ;\n    gap: 20px;\n  }\n\n  .card-event {\n    border: #000000;\n    border-radius: 10px;\n    border-width: 3px;\n    background-color: #b9b9b9;\n    padding: 10px;\n  }\n  .event-info {\n    ul \n    {list-style: none;\n    }\n  }\n#syarat-donor {\n  h2 {\n    text-align: center;\n  }\n}\n.syarat-container {\n  display: grid;\n  grid-template-columns: repeat(2 , 1fr); \n  justify-content: center;\n  align-items: center;\n  padding: 20px 10%;\n  gap: 30px;\n  place-items: center;\n}\n  .syarat-card{\n    padding: 10px;\n    width: 500px;\n    background-color: $red;\n    border-radius: 5px;\n  }\n#keuntungan-donor {\n  h2 {\n    text-align: center;\n  }\n}\n  .keuntungan-container {\n    display: flex;\n    padding: 10%;\n    img {\n   max-width: 300px;\n    }\n    .keuntungan-info {\n      max-width: 800px;\n    }\n  }\n\n  /* CSS untuk form registrasi */\n#registration-form {\n  width: 300px;\n  margin: 0 auto;\n  padding: 20px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n}\n\n#registration-form h2 {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n#registration-form label,\n#registration-form input,\n#registration-form select {\n  display: block;\n  width: 100%;\n  margin-bottom: 10px;\n}\n\n#registration-form input[type=\"text\"],\n#registration-form input[type=\"password\"],\n#registration-form select {\n  padding: 8px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n}\n\n#registration-form input[type=\"submit\"] {\n  width: 100%;\n  padding: 10px;\n  border: none;\n  border-radius: 4px;\n  background-color: #db0000;\n  color: white;\n  font-weight: bold;\n  cursor: pointer;\n}\n#daftar {\n  width: 100%;\n  padding: 10px;\n  border: none;\n  border-radius: 4px;\n  background-color: #db0000;\n  color: white;\n  font-weight: bold;\n  cursor: pointer;\n}\n\n#registration-form input[type=\"submit\"]:hover {\n  background-color: #b80000;\n}\n\n/* CSS untuk form login */\n#login-form {\n  width: 300px;\n  margin: 0 auto;\n  padding: 20px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n}\n\n#login-form h2 {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n#login-form label,\n#login-form input {\n  display: block;\n  width: 100%;\n  margin-bottom: 10px;\n}\n\n#login-form input[type=\"text\"],\n#login-form input[type=\"password\"] {\n  padding: 8px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n}\n\n#login-form input[type=\"submit\"] {\n  width: 100%;\n  padding: 10px;\n  border: none;\n  border-radius: 4px;\n  background-color: #db0000;\n  color: white;\n  font-weight: bold;\n  cursor: pointer;\n}\n\n#login-form input[type=\"submit\"]:hover {\n  background-color: #b80000;\n}\n\n  /* CSS untuk tombol dropdown dan kontennya */\n/* ... (CSS lainnya) */\n\n/* Tombol dropdown */\n#dropdownContainer {\n  display: grid;\n  grid-template-columns: repeat(2 , 1fr);;\n}\n\n.dropbtn {\n  background-color: white;\n  border: none;\n  cursor: pointer;\n\n}\n\n/* Isi dropdown */\n.dropdown-content {\n  display: none;\n  position: absolute;\n  background-color: #f9f9f9;\n  min-width: 16px;\n  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\n  z-index: 1;\n}\n\n/* Tampilkan isi dropdown saat tombol diklik */\n.dropdown:hover .dropdown-content {\n  display: block;\n}\n\n/* Tambahkan gaya untuk link di dalam dropdown */\n.dropdown-content a {\n\n  color: black;\n  min-width: 160px;\n  padding: 12px 16px;\n  text-decoration: none;\n  display: block;\n}\n\n/* Gaya saat link di dalam dropdown dihover */\n.dropdown-content a:hover {\n  background-color: #ffcfcf;\n} \n\n#menu-container {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n "],"sourceRoot":""}]);
